import { zhCN, Translations } from './zh-CN';
import { enUS } from './en-US';
import { useSettingsStore } from '@/stores/settings-store';

const translations: Record<'zh-CN' | 'en-US', Translations> = {
  'zh-CN': zhCN,
  'en-US': enUS,
};

// Hook to use translations
export function useTranslation() {
  const language = useSettingsStore((state) => state.getResolvedLanguage());
  const t = translations[language];

  return { t, language };
}

// Direct translation function (for non-component usage)
export function getTranslation(language: 'zh-CN' | 'en-US') {
  return translations[language];
}

export type { Translations };
